@import '../Common/design.scss';


// All Panels
.all-home-container {
  margin-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 20px;

  // Main

  body {
    font-family: $font-stack;
    color: $primary-color;
    font-size: 1em;
  }

  ul {
    width: 100%;
  }

  .round {
    border-radius: 40px;
  }

  .roundEasy {
    border-radius: 20px;
  }

  .picture-shadow {
    box-shadow: 5px 5px 5px grey;
  }

  .center {
    text-align: center;
    * {
      margin: 0px auto;
    }
  }

}

// Side Panel
.side-panel {
  border-right: solid black 1px;
  margin-right: 15px;

  .profile-img {
    max-width: 200px;
    max-height: 300px;
    width: 100%;
    height: auto;
    object-fit: cover;
    margin: 0px auto;
    padding-right: 10px;
  }

  .name-item {
    padding-top: 10px;
  }

  .external-link-container {
    margin-top: 15px;
  }

  .external-link-icon {
    width: 40px;
    height: 40px;
    margin: 0px 20px 20px 20px;
  }

}
// Main Panel

.title-section {
  border-bottom: solid black 1px;
}

.content-section {
  padding-top: 10px;   
}
.content-section.additional-padding {
  padding-top: 20px;
}

// Education
.education-img {
  max-width: 150px; 
  width: 100%;
  //box-shadow: 1px 1px 6px grey;

}
.education-bullets {
  margin-left: 16px;
}


// Projects

.individual-project {
  min-width: 300px;
  border: solid black 1px;
  padding: 10px;
  margin: 10px;
}

.thumbnail {
  width: 90%;
  height: 140px;
  margin: 15px auto;
}

// Technical Experiences
.logo-wrapper {
  height: 50px;
  justify-content: center;
  display: flex;
}
.company-logo {
  width: 50px;
  margin: auto;
  padding-right: 5px;
}
.individual-experience {
  margin-top: 10px;
  margin-bottom: 5px;
  .logo-col {
    min-width: 50px;
  }
  .no-padding {
    padding-left: 0px;
    padding-right: 0px;
  }
}
.experience-title {
  margin-top: 10px;
  margin-bottom: 0px;
  .no-break {   
    white-space: nowrap;
  }
}
.organization-name {
  color: black;
  padding: 0px 10px;
}
.timeline-info {
  color: gray;
  padding-bottom: 20px;
}